import { Col, Icon, message, Row, Upload } from 'antd';
import { Form } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { Moment } from 'moment';
import * as React from 'react';
import { isMobile } from 'react-device-detect';
import { FormattedMessage } from 'react-intl';

import { Icon as MyIcon } from 'components/Assets';
import { IconValue } from 'components/Assets/Icon';
import { Button } from 'components/Button';
import { SelectBox } from 'components/Invoicing';
import { CollapseCard } from 'components/Invoicing/Card';
import me from 'screens/Invoicing/static/me.json';

import { ICompany, IHeaderInfo } from 'components/Invoicing/types';
import * as AutoSave from 'screens/Invoicing/contexts/AutoSave';

interface IProps extends FormComponentProps, AutoSave.InjectedProps {}

interface IState extends IHeaderInfo {}

class HeaderCard extends React.PureComponent<IProps, IState> {
  state = {
    contact: undefined,
    documentType: undefined,
    logoUrl: undefined,
  };
  private companyInfo: ICompany = {};
  private contacts: string[] = [];

  componentWillMount() {
    const {
      data: {
        me: {
          currentCompany: {
            id,
            name,
            siret,
            vatNumber,
            addresses: { rows },
            contacts,
          },
        },
      },
    } = JSON.parse(JSON.stringify(me));
    this.contacts = contacts.rows.map(
      ({ emails: { rows: contactRows } }: any) => contactRows[0].email,
    );
    this.companyInfo = {
      address1: rows[0].address1,
      address2: rows[0].address2,
      city: rows[0].city,
      id,
      name,
      siret,
      vatNumber,
      zipcode: rows[0].zipcode,
    };
    this.restoreState();
  }

  componentDidUpdate() {
    this.autoSave();
  }

  autoSave() {
    const { updateInvoiceData } = this.props;
    updateInvoiceData({
      emitterCompany: this.companyInfo,
      headerInfo: this.state,
    });
  }

  autoSaveLogoUrl = (logoUrl: string) => {
    const { updateInvoiceData } = this.props;
    updateInvoiceData({ headerInfo: { logoUrl } });
  };

  getAutoSavedUrl = () => {
    const { getAutoSavedData } = this.props;
    const {
      headerInfo: { logoUrl } = { logoUrl: undefined },
    } = getAutoSavedData();
    return logoUrl;
  };

  restoreState = () => {
    const { getAutoSavedData } = this.props;
    const { headerInfo } = getAutoSavedData();
    if (headerInfo) {
      this.setState({
        ...headerInfo,
      });
    }
  };

  contactChanged = (value: any, option: any) =>
    this.setState({
      contact: value,
    });

  documentTypeChanged = (value: any, option: any) =>
    this.setState({
      documentType: value,
    });

  render() {
    const { form } = this.props;
    const { name, siret, vatNumber, address1, address2 } = this.companyInfo;
    const { documentType, contact, logoUrl } = this.state;
    return (
      <CollapseCard
        shadow
        title={<FormattedMessage id="invoicing.detail.header.title" />}
        className={'invoicing-header'}
        titleAlign={'left'}
        collapsable
      >
        <Row>
          <Col
            xs={{ span: 24 }}
            sm={{ span: 8 }}
            md={{ span: 5 }}
            className="mob-mb15"
          >
            <div
              className={`upload-second-logo square ${
                isMobile ? 'bottom-space' : ''
              }`}
            >
              {logoUrl ? (
                <div>
                  <img
                    src={logoUrl}
                    style={{
                      height: '100%',
                      maxHeight: '180px',
                      objectFit: 'contain',
                      width: '100%',
                    }}
                  />
                </div>
              ) : (
                <Button
                  icon={<MyIcon value={IconValue.Plus} />}
                  className="logo-add"
                >
                  <FormattedMessage id="invoicing.detail.header.logo.add" />
                </Button>
              )}
            </div>
          </Col>
          <Col
            xs={{ span: 24 }}
            sm={{ span: 14, offset: 2 }}
            md={{ span: 8, offset: 1 }}
            className="mob-mb15"
          >
            <div>
              <div
                className={`upload-second-logo normal ${
                  isMobile ? 'bottom-space' : ''
                }`}
              >
                <span>{name}</span>
                <p>{address1}</p>
                {address2 && <p>{address2}</p>}
                <p>SIRET : {siret}</p>
                <p>VAT : {vatNumber}</p>
              </div>
              <div className="company-info">
                <MyIcon value={IconValue.Edit} />
                <a>
                  <FormattedMessage id="invoicing.detail.header.edit.companyinfo" />
                </a>
              </div>
            </div>
          </Col>
          <Col
            xs={{ span: 24 }}
            sm={{ span: 24 }}
            md={{ span: 9, offset: 1 }}
            className="selection-group"
          >
            <SelectBox
              defaultValue={contact}
              id="select1"
              onChangeSelect={this.contactChanged}
              title={
                <FormattedMessage id="invoicing.detail.header.dropdown1.title" />
              }
              placeholder="Lucas de Vries"
              options={this.contacts}
              form={form}
            />
            <SelectBox
              defaultValue={documentType}
              id="select2"
              onChangeSelect={this.documentTypeChanged}
              title={
                <FormattedMessage id="invoicing.detail.header.dropdown2.title" />
              }
              placeholder="Select the documentType type"
              options={['Invoice', 'Quote']}
              form={form}
            />
          </Col>
        </Row>
      </CollapseCard>
    );
  }
}

export default Form.create({})(AutoSave.hoc()(HeaderCard));
