import { RcFile, UploadFile } from 'antd/lib/upload/interface';
import { Icon } from 'components/Assets';
import { IconValue } from 'components/Assets/Icon';
import UploadDisplay from 'components/Upload/UploadDisplay';
import 'components/Upload/UploadWrapper.module.less';
import * as React from 'react';
import Dropzone from 'react-dropzone';
import { FormattedMessage } from 'react-intl';
import 'screens/Kyc/Proof.module.less';

interface IProps {
  title: string;
  hasError?: boolean;
  error_text?: string;
  onChange?: (file: UploadFile, base64: string) => void;
  onRemove?: (uid: string) => void;
  onCancel?: (documentId: number) => void;
  onLogoUrlChange: (preview: string) => void;
  getAutoSavedUrl: () => string | undefined;
}

interface IState {
  file?: File;
  preview?: string;
}

class LogoUploader extends React.PureComponent<IProps, IState> {
  static defaultProps = {};

  state = {
    file: undefined,
    preview: undefined,
  };

  handleDisableClick: (e: any) => void;
  handleDrop: (acceptedFiles: File[], rejectedFiles: File[]) => void;
  handleDropAccepted: (acceptedOrRejected: File[]) => void;
  handleCustomRequest: () => void;

  handleRemove: () => void;
  handleBeforeUpload: (file: RcFile, FileList: RcFile[]) => Promise<boolean>;
  constructor(props: any) {
    super(props);

    this.handleDisableClick = this.disableClick.bind(this);
    this.handleDrop = this.drop.bind(this);
    this.handleDropAccepted = this.accepted.bind(this);
    this.handleCustomRequest = this.customRequest.bind(this);

    this.handleRemove = this.onRemove.bind(this);
    this.handleBeforeUpload = this.beforeUpload.bind(this);
  }

  componentWillMount() {
    const { getAutoSavedUrl } = this.props;
    const preview = getAutoSavedUrl && getAutoSavedUrl();
    this.setState({
      preview,
    });
  }

  disableClick(e: any) {
    e.preventDefault();
  }

  customRequest() {
    return { abort: () => {} };
  }

  drop() {}

  async accepted(acceptedOrRejected: File[]) {}

  async beforeUpload(file: RcFile, FileList: RcFile[]): Promise<boolean> {
    this.setLogoUrl(file);
    return false;
  }

  async setLogoUrl(file: File) {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = () => {
      this.setState({
        file,
        preview: reader.result as string,
      });
      const { onLogoUrlChange } = this.props;
      onLogoUrlChange && onLogoUrlChange(reader.result as string);
    };
  }

  onRemove() {
    if (typeof this.state.file !== 'undefined') {
      const file: any = this.state.file;
      const { onLogoUrlChange } = this.props;
      onLogoUrlChange('');
    }
    this.setState({
      file: undefined,
      preview: undefined,
    });
  }

  render() {
    const { hasError, error_text, title, onCancel } = this.props;
    const preview: any = this.state.preview;
    return (
      <Dropzone
        maxSize={5000000}
        accept="image/jpeg, image/jpg, image/png, image/png"
        onDrop={this.handleDrop}
        onDropAccepted={this.handleDropAccepted}
      >
        {({ getRootProps, isDragActive, ...props }) => {
          return (
            <div
              {...getRootProps({})}
              className={`upload-zone active${hasError ? ' has-error' : ''}${
                isDragActive ? ' upload-visible' : 'upload-hidden'
              }`}
            >
              {preview ? (
                <div className="proof-preview">
                  <Icon
                    onClick={this.handleRemove}
                    className="proof-preview-close"
                    value={IconValue.Cross}
                  />
                  <div className="preview-content">
                    <img src={preview} />
                  </div>
                </div>
              ) : (
                <UploadDisplay
                  active
                  small={false}
                  multiple={false}
                  title={title}
                  extension={'kyc.beneficiary.upload_extension'}
                  onCustomRequest={this.handleCustomRequest}
                  onBeforeUpload={this.handleBeforeUpload}
                />
              )}
            </div>
          );
        }}
      </Dropzone>
    );
  }
}

export default LogoUploader;
