import * as React from 'react';
import { FormattedMessage } from 'react-intl';

import { Button } from 'components/Button';
import { Content } from 'components/Layout';
import { IPartners } from 'context/Partners/types';
import { IUser } from 'context/User/types';
import InvoiceDetail from 'screens/Invoicing/Detail';
import 'screens/Invoicing/Invoicing.module.less';
import CreateInvoiceWithNewTemplate from 'screens/Invoicing/New';

interface IProps {
  me: IUser;
  partners: IPartners;
  onSavePreferences: (props: any) => void; // save json preferences
  onSavePdf: (props: any) => void; // request backend to generate pdf
  onSave: (props: any) => void; // on save/update invoice
}
interface IState {
  userHasTemplate: boolean;
}

class Invoicing extends React.PureComponent<IProps, IState> {
  state = {
    userHasTemplate: false,
  };

  onGoToDetail = () => {
    this.setState({ userHasTemplate: true });
  };

  render() {
    const { userHasTemplate } = this.state;
    const { me, partners, onSavePreferences, onSavePdf, onSave } = this.props;
    return (
      <div className="invoicing">
        {userHasTemplate ? (
          <InvoiceDetail />
        ) : (
          <CreateInvoiceWithNewTemplate onGoToDetail={this.onGoToDetail} />
        )}
      </div>
    );
  }
}

export default Invoicing;
