import { Col, Form, Row } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { Moment } from 'moment';
import moment from 'moment';
import * as React from 'react';
import { isMobile } from 'react-device-detect';
import Dragula from 'react-dragula';
import { FormattedMessage } from 'react-intl';
import uuid from 'uuid/v4';

import {
  ICompany,
  IGeneralInfo,
  IInvoiceData,
} from 'components/Invoicing/types';
import * as AutoSave from 'screens/Invoicing/contexts/AutoSave';

import { Icon } from 'components/Assets';
import { IconValue } from 'components/Assets/Icon';
import {
  DateBox,
  InputBox,
  SelectBox,
  TextAreaBox,
} from 'components/Invoicing';
import { H2, Span } from 'components/Typo';
import customers from 'screens/Invoicing/static/partners.json';

interface IProps extends FormComponentProps, AutoSave.InjectedProps {}

interface IState extends IGeneralInfo {}

class DetailGeneralInfo extends React.PureComponent<IProps, IState> {
  state = {
    idCreatedBy: undefined,
    invoiceDate: moment().format('YYYY/MM/DD'),
    invoiceDetails: undefined,
    invoiceDueDate: moment()
      .add(30, 'days')
      .format('YYYY/MM/DD'),
    invoiceNumber: uuid(),
    invoiceTitle: undefined,
  };
  private customerInfos: ICompany[] = [{}];

  componentWillMount() {
    const {
      data: {
        partners: { rows },
      },
    } = JSON.parse(JSON.stringify(customers));
    this.customerInfos = rows.map(
      ({
        id,
        name,
        siret,
        vatNumber,
        addresses: { rows: addressRows },
      }: any) => ({
        address1: addressRows[0].address1,
        city: addressRows[0].city,
        id,
        name,
        siret,
        vatNumber,
        zipcode: addressRows[0].zipcode,
      }),
    );
    this.restoreState();
  }

  autoSave() {
    const { idCreatedBy } = this.state;
    const customerInfo =
      this.customerInfos.find(info => info.id === idCreatedBy) || {};

    const { updateInvoiceData } = this.props;

    updateInvoiceData &&
      updateInvoiceData({
        generalInfo: this.state,
        receiverCompany: customerInfo,
      });
  }

  restoreState = () => {
    const { getAutoSavedData } = this.props;
    const result = getAutoSavedData && getAutoSavedData();
    if (result) {
      const { generalInfo } = result;
      generalInfo && this.setState({ ...generalInfo });
    }
  };

  componentDidUpdate() {
    this.autoSave();
  }

  roundResult = (value: string): string => {
    return parseFloat(value).toFixed(2);
  };

  invoiceTitleChanged = (e: any, value: string) =>
    this.setState({
      invoiceTitle: value,
    });

  customerChanged = (id: any, value: any) => {
    this.setState({
      idCreatedBy: id,
    });
  };

  detailChanged = (e: any, value: string) =>
    this.setState({
      invoiceDetails: value,
    });

  dateChanged = (date: Moment, dateString: string) =>
    this.setState({
      invoiceDate: dateString,
    });

  dueDateChanged = (date: Moment, dateString: string) =>
    this.setState({
      invoiceDate: dateString,
    });

  render() {
    const { form } = this.props;
    const {
      invoiceNumber,
      idCreatedBy,
      invoiceDate,
      invoiceDueDate,
      invoiceTitle,
      invoiceDetails,
    } = this.state;
    const customerNames = this.customerInfos.map(customer => customer.name);
    const selectedCustomer = this.customerInfos.find(
      customer => customer.id === idCreatedBy,
    );
    const customerIds = this.customerInfos.map(customer => customer.id);

    return (
      <>
        <Row className="preference">
          <Col xs={{ span: 24 }} sm={{ span: 14 }} md={{ span: 10 }}>
            <InputBox
              defaultValue={invoiceTitle}
              onChange={this.invoiceTitleChanged}
              title={
                <FormattedMessage
                  id={'invoicing.detail.body.invoice.input.invoiceTitle'}
                />
              }
              placeholder="Enter invoice object"
              form={form}
              id="text1"
            />
          </Col>
        </Row>
        <Row>
          <Col xs={{ span: 24 }} sm={{ span: 11 }} md={{ span: 6 }}>
            <SelectBox
              id="select3"
              title={
                <FormattedMessage id="invoicing.detail.body.dropdown.invoiceTitle" />
              }
              placeholder="Select Customer"
              form={form}
              partnerIds={customerIds}
              defaultValue={selectedCustomer && selectedCustomer.name}
              options={customerNames}
              onChangeSelect={this.customerChanged}
            />
          </Col>
          <Col
            xs={{ span: 24 }}
            sm={{ span: 11, offset: 2 }}
            md={{ span: 7, offset: 8 }}
          >
            <InputBox
              title={
                <FormattedMessage
                  id={'invoicing.detail.body.invoice.invoiceNumber'}
                />
              }
              form={form}
              id="text2"
              disabled={true}
              defaultValue={invoiceNumber}
            />
          </Col>
        </Row>
        <Row type="flex">
          <Col xs={{ span: 24 }} md={{ span: 12 }} className="mt-1">
            <TextAreaBox
              id="textarea1"
              defaultValue={invoiceDetails}
              title={
                <FormattedMessage id="invoicing.detail.body.invoice.invoiceDetails" />
              }
              placeholder="Lora Ipsum blah blah"
              form={form}
              onChange={this.detailChanged}
            />
          </Col>
          <Col xs={{ span: 24 }} md={{ span: 7, offset: 2 }}>
            <div className="input-group">
              <DateBox
                defaultValue={moment(invoiceDate, 'YYYY/MM/DD')}
                id="date1"
                title={
                  <FormattedMessage
                    id={'invoicing.detail.body.invoice.invoiceDate'}
                  />
                }
                form={form}
                onChangeDate={this.dateChanged}
              />
              <DateBox
                id="date2"
                defaultValue={moment(invoiceDueDate, 'YYYY/MM/DD')}
                title={
                  <FormattedMessage id={'invoicing.detail.body.paymentdue'} />
                }
                form={form}
                onChangeDate={this.dueDateChanged}
              />
            </div>
          </Col>
        </Row>
      </>
    );
  }
}

export default Form.create({})(AutoSave.hoc()(DetailGeneralInfo));
